@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /*border: 2px solid red;*/

  font-family: "Montserrat", sans-serif;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.card:hover {
  box-shadow: 5px 10px 20px 10px rgba(9, 9, 121, 1) !important;
  transition: 0.5s ease-in-out !important;
}

/* NAVBAR CSS */

.bi-list {
  color: rgba(0, 212, 255, 1) !important;
  height: 30px;
  width: 30px;
}

.navbar {
  background: transparent;
  top: 0;
  z-index: 999;
  height: 85px;
}

.navbar.navbar-light .navbar-nav .nav-link {
  color: white;
  font-weight: 600;
  position: relative;
}

.navbar.active {
  background: white;
}

.navbar.active.navbar-light .navbar-nav .nav-link {
  color: gray;
  font-weight: 600;
  position: relative;
}

.navbarnav {
  margin-left: auto;
}

.nav-link {
  margin-right: 40px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 400;
  color: white;
}

.nav-link:hover {
  color: rgba(0, 212, 255, 1) !important;
}

.navbar-toggler {
  border-color: rgba(0, 212, 255, 1) !important;
}

.nav-row {
  margin-left: 0;
  margin-right: 0;
  position: fixed;
  top: 0;
  width: 100%;
  height: 70px;
  z-index: 2;
  display: block;
}

/* HOME CSS */

.wallpaper {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-image: url("https://onlineplus.com.au/wp-content/uploads/2016/11/slider-background-1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.content {
  margin-top: 240px;
  color: #fff;
  text-align: center;
  font-size: 56px;
}

.content2 {
  text-align: center;
  color: #fff;
  line-height: 1.9;
  font-size: 18px;
}

.content3 {
  text-align: center;
  color: #fff;
  line-height: 1.9;
  font-size: 18px;
}

.sub {
  font-size: 27px;
  letter-spacing: 2px;
  color: #fff;
  text-align: center;
}

/* ABOUT CSS */

.about-me::after {
  content: "";
  background: rgba(0, 212, 255, 1);
  height: 5px;
  width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.about-circle-img {
  height: auto;
  width: 100%;
  max-height: 300px;
  max-width: 200px;
}

.content-about {
  text-align: left;
  color: #574f5e;
  line-height: 1.9;
  font-size: 18px;
  font-weight: 600;
}

.about-me-content {
  align-items: center;
  justify-content: center;
  display: grid;
}

/* SKILLS */

.skill {
  background: rgb(9, 9, 121);
  background: linear-gradient(
    90deg,
    rgba(9, 9, 121, 1) 0%,
    rgba(0, 212, 255, 1) 100%
  );
}

.content-skill {
  font-size: 15px;
  text-align: center;
}

.bootstrap-icon {
  max-height: 73px;
  width: auto;
}

.css-icon {
  max-height: 90px;
  width: auto;
}

.express-icon {
  width: 120px;
  height: 35px;
}

.javascript-icon {
  max-height: 80px;
  max-width: 160px;
}

.nodejs-icon {
  max-height: 80px;
  max-width: 80px;
}

.hosting-icon {
  max-height: 70px;
  width: auto;
}

/* EXPRIENCE CSS */

.img-resize {
  width: 10em;
}

.style {
  font-size: 20px;
}

.body-title-style {
  font-size: 25px;
  font-weight: 500;
}

.about-me2::after {
  content: "";
  background: #fff;
  height: 5px;
  width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* FOOTER CSS */

.footer {
  background: linear-gradient(to right, #31092a, #0e0331);
  color: white;
}

.footer-content {
  text-align: center;
}

.social-logo {
  width: 30px;
  height: 30px;
}

.social-logo:hover {
  transform: translateY(-5px);
}

.image:hover {
  transition: 0.5s ease-in-out !important;
  filter: blur(4px) !important;
}

